.creatorImageWrapper{
    width: 5em;
    height: 5em;
    align-self: center;
}

.creatorImage{
    content: "";
    display: block;
    width: 100%;
    height:0;
    padding-bottom: 100%;
    background: #BFBFBF;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
}
.creatorImageText{
    float:left;
    width:100%;
    padding-top:50%;
    line-height:3em;
    margin-top:-3em;
    text-align:center;
    color:white;
}
.creatorImageText p{
    text-align: center;
    font-size: 24px;
}