.button {
    padding: 0 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #FFFFFF;
    border: 1px solid #F0F0F0;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 6px;
    margin: 1em 0 1em 0;
}

.link {
    text-decoration: none;
    font-weight: 600;
    line-height: 24px;
    color: #262626;
}

.text {
    font-size: 16px;
}

.price {
    font-size: 18px;
}

.icon {
    margin-bottom: -3px;
}