.wrapper {
    border: 1px solid #D9D9D9;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 8px;
    padding: 14px 14px 10px;;
}

.info {
    padding: 15px 0;
}

.title {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
}
.price {

    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
}

.price span {
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
}

.button {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px 16px;
    background: #32AD72;
    border-radius: 2px;
    font-weight: 600;
    font-size:.8em;
    line-height: 22px;
    color: white;
}