@keyframes slide-up {
    from {
        margin-top: 100%;
        height: 300%;
    }
    to {
        margin-top: 0%;
        height: 100%;
    }
}

.modal {
    padding: 1rem;
    top: 20%;
    position: absolute;
    margin-left: 1rem;
    margin-right: 1rem;
    left: 0;
    right: 0;
    background: #FFFFFF;
    box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 3px 6px -4px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    animation: .2s ease-in slide-up;
}

.title {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #262626;
}

.price {
    font-weight: 600;
    font-size: 38px;
    line-height: 15px;
    text-align: center;
    color: #262626;
}

.buttons {
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.payTo {
    display: flex;
    gap: 5px;
    align-items: baseline;
}

.messageIcon {
    display: flex;
    align-self: center;
}

.chatIcon {
    width: 32px;
    height: 32px;
    border-radius: 96px;
    background: #BFBFBF;
    text-align: center;
    justify-content: center;
}

.chatIcon span {
    width: 32px;
    height: 32px;
    font-weight: 600;
    font-size: 16px;
    line-height: 32px;
    color: #FFFFFF;
}

@keyframes goRound {
    0%{
        transform:rotate(0deg)
    }
    100%{
        transform:rotate(360deg)
    }
}

.loader {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #FFFFFF;

}

.loaderIcon {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    animation: 1s linear infinite goRound;
}

.modalLoaderIcon {
    animation: 1s linear infinite goRound;
}

.doneIcon {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}