.highlighted {
    padding: 0px 16px;
    gap: 8px;
    width: 100%;
    background: #262626;
    border-radius: 2px;
    border: 1px solid #D9D9D9;
}

.highlighted p {
    font-weight: 400;
    font-size: 16px;
    line-height: 10px;
    color: #FFFFFF;
}

.button {
    padding: 0px 16px;
    gap: 8px;
    width: 100%;
    background: transparent;
    border: 1px solid #D9D9D9;
    border-radius: 2px;
}

.button p {
    font-weight: 400;
    font-size: 16px;
    line-height: 10px;
    color: #434343;
}