.wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 8px;
    margin: 1rem;
}

.video {
    width: 10rem;
    height: 16rem;
    background: #F5F5F5;
    border: 2px solid #1890FF;
    border-radius: 12px;
    position: relative;
}

.photoPreview {
    display: block;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    height: 100%;
    object-fit: cover;
    max-width: 100%;
    border-radius: 10px;
}

.videoPreview video{
    display: block;
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 0px;
    max-width: 100%;
    object-fit: cover;
    border-radius: 10px;
    z-index: 0;
}

.dateText {
    padding-top: 120%;
    text-align: right;
    padding-right: 20px;
    z-index: 1;
    position: relative;
}

.dateTextWhite {
    padding-top: 120%;
    text-align: right;
    padding-right: 20px;
    z-index: 1;
    position: relative;
    color: white;
}

.chatIcon {
    width: 32px;
    height: 32px;
    border-radius: 96px;
    flex: none;
    order: 1;
    flex-grow: 0;
    background: #FF7A45;
}

.chatIcon span {
    width: 32px;
    height: 32px;
    font-weight: 600;
    font-size: 16px;
    line-height: 32px;
    color: #FFFFFF;
}