.tabsWrapper{
}

.reactTabs ul{
    display: flex;
    justify-content: center;
    border: none;
    list-style: none;
    text-align: center;
    padding: 0;
    border-bottom: 1px solid #F0F0F0;
}

.reactTabs ul li{
    padding: 0 10px 10px 10px;
}

.reactTabs ul li[aria-selected="true"]{
    border-bottom: 2px solid #1890FF;
    fill: #1ea7fd;
}

.reactTabs ul li[aria-selected="true"] path{
    fill: #1ea7fd;
}

.cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 15px;
}
