.wrapper {
    margin: 5rem 1em 0 1em;
}

.text {
    margin-top: 2em;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    text-align: center;
    color: #262626;
}

.buttons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

@-webkit-keyframes glow { from { border: 6px solid #FFFFFF; } to { border: 6px solid #BAE7FF; }  }
@keyframes glow { from { border: 6px solid #FFFFFF; } to { border: 6px solid #BAE7FF; }  }


.glowing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 16px;
    gap: 8px;
    width: 44px;
    height: 44px;
    background: #1890FF;
    -webkit-animation: 2s ease-in 0s infinite alternate glow;
    animation: 2s ease-in 0s infinite alternate glow;
    border-radius: 1000px;
    position: relative;
}

.uploadField {
    opacity: 0;
    display: block;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
}

.glowingIcon {
    display: flex;
    align-content: center;
    justify-content: center;
}