.button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 12px;
    gap: 8px;
    width: 35px;
    height: 35px;
    background: #FFFFFF;
    border: 1px solid #FF7875;
    border-radius: 1000px;
}

.link {
    text-decoration: none;
    font-weight: 600;
    line-height: 24px;
    color: #262626;
}

.icon {
    margin-bottom: -3px;
}

@-webkit-keyframes glow { from { border: 6px solid #FFFFFF; } to { border: 6px solid #BAE7FF; }  }
@keyframes glow { from { border: 6px solid #FFFFFF; } to { border: 6px solid #BAE7FF; }  }

.glowing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 16px;
    gap: 8px;
    width: 44px;
    height: 44px;
    background: #1890FF;
    -webkit-animation: 2s ease-in 0s infinite alternate glow;
    animation: 2s ease-in 0s infinite alternate glow;
    border-radius: 1000px;
}
