.header {
    width: 100%;
    background: #FFFFFF;
    border-radius: 0px;
}

.headerText {
    color: #333333;
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
    border-bottom: 2px solid #F0F0F0;
}

.priceText {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #262626;
}

.priceText span {
    font-size: 1.5em;
    font-weight: 600;
}