.profileWrapper{
    width: 2em;
    height: 2em;
}

.userProfile{
    content: "";
    display: block;
    width: 100%;
    height:0;
    padding-bottom: 100%;
    background: #BFBFBF;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
}
.profileText{
    float:left;
    width:100%;
    padding-top:50%;
    line-height:1.6em;
    margin-top:-1.6em;
    text-align:center;
    color:white;
}
.profileText p{
    text-align: center;
    font-size: 12px;
}